
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import StudentList from './list.vue'
import PopupFilter from './popup-filter.vue'

import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default{
mixins: [ common ],
components: {
Card,
Flexbox,
FlexboxItem,
PopupFilter,
StudentList
},
data () {
return {
data: [],
classList: [],
search: {
student_name: '',
order_field: 'student_lesson_remain_hours',
order_type: 'asc',
cid: []
},
map_sex: {0:'未确定',1:'男',2:'女'},
map_student_type: {0:'体验学员 ',1:'正式学员 ',2:'vip学员'},
map_status: {1:'正常',20:'停课',30:'休学',50:'结课',90:'退学',100:'封存'}
}
},
mounted () {
this.init()
},
computed: {
style () {
let top = this.headerHeight + 88
return {
top: `${top}px`
}
},
data_uri () {
return `employees/${this.eid$}/students`
},
},
methods: {
hook_get_param (param) {
let search = this.search
param.class = 1
for(let o in search){
if(o == 'cid'){
if(search[o].length > 0){
param[o] = String(search[o])
}
}else if(search[o]){
param[o] = search[o]
}
}

},
serviceDetail (item) {
this.$router.push({path:'./service/'+item.sid})
},
getAge (item) {
return util.age(item.birth_time)
},
detail (item) {
this.$router.push({path:'./student/'+item.sid})
},
init () {
this.getMyClass()
},
getMyClass () {
this.$rest(`classes?pagesize=1000&teach_eid=${this.eid$}`).get()
.success(data=>{
this.classList = data.list
}).error(response=>{

})
},
openFilter () {
this.$refs.filter.show()
},
sortClick (field) {
if(this.search.order_field == field){
this.search.order_type = this.search.order_type=='asc'?'desc':'asc'
}else{
this.search.order_field = field
this.search.order_type = 'asc'
}
this.downCallback()
},
isActive (field,type) {
let result = '',
search = this.search

if(type){
result = (search.order_field==field&&search.order_type==type)?'active':''
}else{
if(this.search.order_field==field){
result = 'active'
}
}

return result
},
sortData (data, type, key) {
data.sort((a, b) => {
if (type === 'asc') {
return Number(a[key]) > Number(b[key]) ? 1 : -1;
} else if (type === 'desc') {
return Number(a[key]) < Number(b[key]) ? 1 : -1;
}
});
return data;
},
resetSearch () {
this.search.student_name = ''
this.searchFocus()
this.downCallback()
},
searchFocus () {
this.$refs.search.focus()
}
}
}



